@import '../../../styles/commons';

.lessons-card {
    border: 1px solid #DDD;
    margin: 10px 10px 10px 0;
    padding: 10px;
    background-color: white;
    opacity: .6;
    -webkit-transition: opacity $transition-quick linear;
    -o-transition: opacity $transition-quick linear;
    transition: opacity $transition-quick linear;
    -ms-flex: 0 0 auto;
        flex: 0 0 auto; /* add this for older Safari version */

    &:nth-child(1) {
        margin-left: 10px;
    }

    .lessons-card-select-container {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: end;
            justify-content: flex-end;
        -ms-flex-align: center;
            align-items: center;
        font-size: 1rem;

        span {
            margin-right: 10px;
        }

        input[type=checkbox] {
            height: 20px;
            width: 20px;
            cursor: pointer;
            position: relative;
        }
    }

    .lessons-card-header {
        max-width: 300px;

        h3 {
            text-align: center;
            font-size: 1.5rem;
            -webkit-transition: color $transition-quick linear;
            -o-transition: color $transition-quick linear;
            transition: color $transition-quick linear;
        }

        h4 {
            text-align: center;
            font-size: 1.3rem;
            font-weight: 300;
        }
    }

    ul {
        list-style-type: none;
        padding: 0;
        margin: 0;
    }

    &.selected {
        opacity: 1;

        .lessons-card-header h3 {
            color: $green;
        }
    }
}



